body {
  background-color: white;
  display: flex;
  min-height: 100vh;
  justify-content: center;
  align-items: center;
}

.form-container {
  width: 360px;
  background-color: white;
  margin: auto;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
  padding: 10px;
}

.register-form {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 10px;
}

.success-message {
  font-family: "Roboto", sans-serif;
  background-color: #3f89f8;
  padding: 15px;
  color: white;
  text-align: center;
}

.form-field {
  margin: 10px 0 10px 0;
  padding: 15px;
  font-size: 16px;
  border: 0;
  font-family: "Roboto", sans-serif;
}

span {
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  color: red;
  margin-bottom: 15px;
}

input {
  background: #f2f2f2;
}

.error {
  border-style: solid;
  border: 2px solid #ffa4a4;
}

button {
  background: #4caf50;
  color: white;
  cursor: pointer;
}

button:disabled {
  cursor: default;
}

.page {
  width: 100vw;
  overflow-x: hidden;
}

.navbar {
  position: fixed;
  top: 0%;
  width: 100vw;
  display: flex;
  justify-content: flex-end;
  height: 10%;
  align-items: center;
  background-color: #dcf1f7;
}

.logout-btn {
  padding-right: 2%;
}

.content {
  width: 100%;
  display: flex;
}

.sidebar {
  width: 20%;
  height: 100vh;
  /* background-color: #4caf50; */
}

.main-content {
  width: 80%;
  height: 100vh;
  /* background-color: salmon; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

.product-grid {
  width: 90%;
  height: 70%;
  display: flex;
  justify-content: center;
}

.product-page {
  /* background-color: brown; */
  width: 100%;
  height: 80%;
  border-radius: 5%;
  cursor: pointer;
  transition: all ease-in-out 0.1s;
  box-shadow: 2px 2px #e8dcdc;
  padding: 2%;
  position: relative;
}

.wrapper {
  width: 100%;
  height: 100%;
  padding-top: 3%;
  /* background-color: #4caf50; */
}

.product-page:hover {
  transform: scaleY(1.05);
}

.image-container {
  width: 90%;
  height: 50%;
  display: flex;
  justify-content: center;
}

.image-container>img {
  width: 60%;
  height: 90%;
}

.title {
  width: 100%;
  height: 20%;
  display: flex;
  justify-content: center;
  padding-left: 3%;
  padding-right: 3%;
  font-family: 'Arial', sans-serif;
  font-size: 20px;

}

.price {
  width: 100%;
  height: 15%;
  padding-left: 5%;
  font-size: 20px;
  color: tomato;
  font-weight: bolder;
}

.rating {
  width: 100%;
  height: 15%;
  padding: 0%;
  padding-left: 5%;
  font-size: 40px;
  /* color: tomato; */
  font-weight: bolder;
}

.sidebar {
  display:  flex;
  justify-content: center;
  align-items: center;
}


.slider-box {
  width: 70%;
  border: #7a7473 solid 5px;
  padding: 2% 5%;
  
}